<template>
  <div id="app">
    <audio ref="audioSoco" src="./assets/audio-soco.mp3"></audio>
    <saco v-bind:isSocoDado = isSoco v-bind:estourado = sacoFurado > </saco>
    <vida v-bind:barraDeVida = barraVida></vida>
    <botao v-bind:vidaVazia = vidaZero @soco = socoDado @reinicio= reiniciar>  </botao>
  </div>
</template>

<script>
import saco from './components/sacoPancada.vue'
import vida from './components/Vida.vue'
import botao from './components/botoes.vue'

export default {
  name: 'App',
  components: {
    'saco': saco,
    'vida': vida,
    'botao': botao
  },
  data: function(){
    return {
      valorVida: 100,
      barraVida: "width: 100%",
      vidaZero: false,
      sacoFurado: false,
      isSoco: false    
    }
  },
  methods: {
    socoDado: function(){
      this.$refs.audioSoco.play();

      if(this.valorVida > 10){
        this.valorVida = this.valorVida - 10;
        this.barraVida = "width: "+this.valorVida+"%";

        this.isSoco = true;
        setTimeout(() => this.isSoco = false, 500);
      }else{
        this.valorVida = this.valorVida - 10;
        this.barraVida = "width: "+this.valorVida+"%";
        this.vidaZero = true;
        this.sacoFurado = true;
      }     
    },

    reiniciar: function(){
      this.valorVida = 100;
      this.barraVida = "width: "+this.valorVida+"%";
      this.vidaZero = false;
      this.sacoFurado = false;      
    },

  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
