<template>
    <div> 
        
        <button v-if="!vidaVazia" @click = socar>Socar</button>
        <button @click = reiniciar>Reiniciar</button>
    </div>
</template>

<script>
export default {
 methods:{
     socar: function(){
         this.$emit('soco');
     },

     reiniciar: function(){
         this.$emit('reinicio');
     }
 },

 props:{
     vidaVazia: Boolean
 }   
}
</script>

<style>
button{
    margin: 5px;
}
</style>
