<template>    
    <div>
        <img v-bind:class="{socoVai: isSocoDado, socoVolta: !isSocoDado}" 
            v-bind:src = urlSaco alt="Saco de pancada">
    </div>        
</template>

<script>
export default {   
    props: {
        estourado: Boolean,
        isSocoDado: Boolean        
    },

    computed:{
        urlSaco: function(){
            if(this.estourado){
                return require("../assets/saco-estourado.png")
            }else{
                return require("../assets/saco-normal.png")
            }
        }
    }    
}
</script>

<style>
img {
    height: 400px;
}

.socoVai {
    transform: rotateY(0deg) rotate(45deg);
    transition: transform 0.5s;
}

.socoVolta {
    transform: rotateY(0deg) rotate(0deg);
    transition: transform 0.5s;
}
</style>